@import url(https://fonts.googleapis.com/css2?family=Fira+Sans&family=Lato&family=Mukta:wght@300&family=Nunito+Sans&family=Poppins:wght@200&family=Ubuntu&display=swap);
*{
  margin:0;
  padding: 0;
}

.root-bg{
  background-color: orange;
  height: 100vh;
  width: 100vw;
}

.navlinkStyle {
  text-decoration: none;
  color: gray;
  margin: 0 0.5em;
  transition-duration: 1s;
}

.navlinkStyle:hover {
  color: black;
  size: 2em;
}

.bi {
  position: fixed;
  bottom: 4em;
  right: 3em;
  color: green;
  cursor: pointer;
  transition-property: all;
  transition-duration: 1s;
  font-size: 1.3em;
}

.bi:hover {
  font-size: 1.7em;
}

#card {
  background-image: radial-gradient(rgb(237 140 218), rgb(206 155 225));
  box-shadow: 0.5em 0.5em 0.5em;
}

#card-content {
  color: rgb(254 254 254);
}

#container-html {
  align-items: center;
  display: flex;
  justify-content: center;
  text-align: center;
  height: 80vh;
}

#email {
  color: blue;
  outline: none;
  border: none;
  padding: 0.8em;
}

#password {
  outline: none;
  border: none;
  padding: 0.8em;
}

.btn-html {
  width: 17em;
  border: none;
  border-radius: 11em;
  height: 2em;
  cursor: pointer;
  background-image: linear-gradient(130deg, #79bbe1, #da48f9);
  margin-bottom: 1.5em;
}

.input {
  border: none;
  margin-bottom: 2em;
  border-bottom: 1px solid gray;
}

h2 {
  margin-bottom: 2em;
}

.fa-brands {
  cursor: pointer;
  color: #0d6efd;
}

.social-icons {
  color: black;
}

#full-blog-page{
  background-image: linear-gradient(45deg, #758db3, transparent);
  margin: auto;
  border-radius: 3em;
}

#blog-body{
  margin: auto;
  width: 90vw;
  font-family: 'Ubuntu', sans-serif
}

#blog-title{
  font-size: 2em;
  font-weight: bold;
  margin: 0.5em;
  text-align: center;
  font-family: 'Lato', sans-serif
}

*{
  margin: 0;
  padding: 0;
}

#entire-addBlog{
  height: 100vh;
  width: 100vw;
  /* background-color: #ffebe9; */
  background-image: linear-gradient(45deg, #fd9b3d, transparent);
  /* background-image: url(https://png.pngtree.com/png-vector/20200907/ourmid/pngtree-abstract-modern-blue-purple-wave-background-png-image_2339850.jpg);
  background-repeat: no-repeat;
  background-size: cover; */
  border-radius: 0.4em;
  display: flex;
  justify-content: center;
  align-items: center;
}

#addBlog-form{
  height: 85vh;
  width: 40vw;
  background-image: linear-gradient(173deg, #f3bd5a, transparent);
  padding: 0em 3em;
  border-radius: 2em;
}

#addBlog-form input{
  width: 28em;
  margin: 0.8em 0em;
  border: 0.2em solid gray;
  border-radius: 0.8em;
  height: 3em;
 
  padding: 0.4em
}

#addBlog-form textarea{
  width: 28em;
  border: 0.2em solid gray;
  border-radius: 0.8em;
  padding: 0.4em
}

.blog-default{
  text-align: center;
  margin-top: 2em ;
  margin-bottom: 1.5em;
  color: navy;
}

#blog-submit{
  display: block;
  margin: auto;
  width: 15em;
  height: 3em;
  border: 0.1em solid green;
  background-color: white;
  border-radius: 1em;
  margin-top: 1em;
  cursor: pointer;
  font-family: 'Nunito Sans', sans-serif;
}

 #entire-addBlog button{
  /* transition: all;
  transition-duration: 1s; */
  padding: 0.6em;
  background-color: rgb(124, 216, 124);
}
